import React from 'react'
import { site_url } from '../config'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import Loadable from 'react-loadable'

const Loading = () => null

const SplashScreen = Loadable({
  loader: () => import( '../components/pages/splash-screen' ),
  loading: Loading
})

const SignIn = Loadable({
  loader: () => import( '../components/pages/signin' ),
  loading: Loading
})

const SignUp = Loadable({
  loader: () => import( '../components/pages/signup' ),
  loading: Loading
})

const Dashboard = Loadable({
  loader: () => import( '../components/pages/dashboard' ),
  loading: Loading
})

const Profile = Loadable({
  loader: () => import( '../components/pages/profile' ),
  loading: Loading
})

const Success = Loadable({
  loader: () => import( '../components/pages/success' ),
  loading: Loading
})

const Mur = Loadable({
  loader: () => import( '../components/pages/mur' ),
  loading: Loading
})

const BalanceIn = Loadable({
  loader: () => import( '../components/pages/dashboard/balance-in' ),
  loading: Loading
})

const BalanceOut = Loadable({
  loader: () => import( '../components/pages/dashboard/balance-out' ),
  loading: Loading
})

const RouteBeforeLogin = (
  <BrowserRouter>
    <Switch>
      <Route exact path={site_url.signin} component={SignIn} />
      <Route exact path={site_url.signup} component={SignUp} />
      <Route exact path={site_url.dashboard} component={Dashboard} />
      <Route exact path={site_url.balance_in} component={BalanceIn} />
      <Route exact path={site_url.balance_out} component={BalanceOut} />
      <Route exact path={site_url.profile} component={Profile} />
      <Route exact path={site_url.success} component={Success} />
      <Route exact path={site_url.mur} component={Mur} />
      <Route exact path={site_url.blank} component={SplashScreen} />
    </Switch>
  </BrowserRouter>
)

export default RouteBeforeLogin
