import ReactDOM from 'react-dom'
import { Cookies, siteCookie } from './config'
import RouteBeforeSign from './routers/before-login'
import RouteAfterSign from './routers/after-login'
import * as serviceWorker from './serviceWorker'
import './assets/style.css'

if ( Cookies.get( siteCookie.Token ) ){
  ReactDOM.render( RouteAfterSign, document.getElementById( 'root' ) )
} else {
  ReactDOM.render( RouteBeforeSign, document.getElementById( 'root' ) )
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register()
